.main{
	height: 100%;
	padding-top: 60px;
}
.main-container{
	margin: 0 auto;
	width: 95%;
}
.main-logo{
	width: 100%;
}
.twitter-embed{
	float: right;
	width: 25%;
}
[id*="twitter-widget-"]{
	display: block;
	width: 100%;
	min-width: 0 !important;
}
.main-contents{
	display: block;
}
.main-movie{
	text-align: center;
}
.contents,.top-contents{
	padding: 10px 20px;
}
.contents{
	margin-bottom: 30px;
	background-color: white;
	color: black;
	box-shadow: 0px 0px 3px;
	border-radius: 4px;
}
.pending-contents{
	color: white;
	font-size: 2rem;
	text-align: center;
	padding: 50px 50px;
}
.contents-half{
	display: inline-block;
	width: calc((100% - 100px) / 2);
	vertical-align: top;
}
.contents-left{
	margin-right: 20px;
}
.description-title, .navbar-text, .contents-title{
	font-family: 'Times New Roman';
	font-weight: lighter;
}
.description-title{
	color: white;
	font-size: 50px;
}
.description{
	font-family: 'IPAex mincho', 'Yu Mincho Light','YuMincho','Yu Mincho','游明朝体';
	font-weight: 400;
	color: white;
	font-size: 1.4rem;
}
.news-article{
	border-top: 1px dashed;
	padding: 10px;
}
.movie-title{
	font-size: 50px;
	color: white;
	font-family: 'IPAex mincho', 'Yu Mincho Light','YuMincho','Yu Mincho','游明朝体';
}
.movie-embed{
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 90%;
	height: 100%;
}
.movie-wrapper{
	position: relative;
	width: 100%;
}
.movie-wrapper:before{
	content: "";
	display: block;
	padding-top: 56.25%;
}