.castboxes{
	text-align: center;
	vertical-align: top;
}
.castbox-kind{
	font-family: 'IPAex mincho', 'Yu Mincho Light','YuMincho','Yu Mincho','游明朝体';
	color: white;
	font-size: 3vmax;
}
.castbox{
	font-family: '游ゴシック体';
	font-weight: 500;
	background-color: white;
	vertical-align: top;
	width: 250px;
	height: 280px;
	border: solid;
	border-width: 1px;
	border-radius: 10px;
	padding: 10px;
	margin: 20px;
}
.castbox-name{
	font-size: 30px;
}
.castbox-img{
	height: 180px;
}
.castbox-description{
	font-size: 16px;
}
.modal-main{
	text-align: center;
	height: 100%;
	color: white;
}
.modal-name{
	text-align: center;
	font-size: 30px;
}
.modal-img{
	width: 180px;
}